@family {
  font-family: 'Open Sans', 'Arial' sans-serif;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 0px;
  height: 0px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
.hidden-image {
  display: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
.page-header {
  margin: 0 auto;
}
.page-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 1159px;
  min-height: 100px;
  margin: 0 auto;
}
.navigation-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  min-width: 479px;
}
.navigation-list__item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.navigation-list__item-link {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  text-decoration: none;
  color: #141517;
}
.navigation-list__item-link--prices {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  text-decoration: none;
  color: #fe7865;
}
.navigation-list__item-phone {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
  text-decoration-color: #fe7865;
  color: #fe7865;
  padding-left: 25px;
}
.navigation-list__item-phone::before {
  content: '';
  position: absolute;
  background: url(../img/phone-icon.svg) no-repeat;
  background-size: 17px 18px;
  background-position: center;
  width: 30px;
  height: 30px;
  top: 0px;
  left: -10px;
}
.promo-top__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  background: url(../img/promo-top-background.jpg) no-repeat;
  background-size: cover;
  width: 100%;
  min-width: 1162px;
  min-height: 860px;
}
.promo-top__heading {
  font-family: 'Montserrat', sans-serif;
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
  color: #ffffff;
  max-width: 412px;
  margin-right: 433px;
}
.promo-top__description {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  color: #ffffff;
  max-width: 376px;
  margin-top: 30px;
  margin-right: 469px;
}
.promo-top__link {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  width: 300px;
  height: 75px;
  background-color: #fe7865;
  padding-left: 40px;
  margin-top: 39px;
  margin-right: 505px;
}
.promo-top__link::after {
  content: '';
  position: absolute;
  background: url(../img/arrow-right.svg) no-repeat;
  background-size: 14px 14px;
  width: 14px;
  height: 14px;
  top: 31px;
  left: 286px;
}
.promo-places {
  margin: 0 auto;
}
.promo-places__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding-top: 170px;
}
.promo-places__heading {
  font-family: 'Montserrat', sans-serif;
  font-size: 55px;
  line-height: 80px;
  font-weight: 500;
  color: #2c2e3f;
  width: 358px;
  margin-bottom: 64px;
}
.promo-places__description {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: #2c2e3f;
  opacity: 0.7;
  width: 358px;
  margin-bottom: 50px;
}
.promo-places__description::before {
  content: '';
  position: absolute;
  width: 229px;
  height: 4px;
  background-color: #fe7865;
  top: -35px;
  left: 1px;
}
.places-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}
.places-list__item {
  display: flex;
  justify-content: center;
  background-color: yellow;
  width: 350px;
  height: 430px;
  margin-right: 60px;
  margin-bottom: 100px;
}
.places-list__item:nth-child(3n) {
  margin-right: 0px;
}
.places-list__item:nth-child(4) {
  margin-left: 185px;
}
.places-item--greece {
  background: url(../img/greece.jpg) no-repeat;
}
.places-item--albania {
  background: url(../img/albania.jpg) no-repeat;
}
.places-item--macedonia {
  background: url(../img/macedonia.jpg) no-repeat;
}
.places-item--montenegro {
  background: url(../img/montenegro.jpg) no-repeat;
}
.places-item--croatia {
  background: url(../img/croatia.jpg) no-repeat;
}
.places-item__link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  line-height: 30px;
  font-weight: 500;
  background-color: #ffffff;
  color: #2c2e3f;
  text-decoration: none;
  width: 215px;
  height: 61px;
  margin-top: 370px;
}
.slideshow-container {
  width: 1220px;
  min-height: 899px;
  margin: 0 auto;
}
.promo-feedback__dots {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 870px;
}
.dot {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: #2c2e3f;
  opacity: 0.3;
  cursor: pointer;
}
.slideshow-container__slides {
  height: 890px;
  background: url(../img/Group\ 29.png) no-repeat;
  background-position: bottom;
  background-size: cover;
  margin-top: 110px;
}
.slide-greece {
  background: url(../img/greece-slide.jpg) no-repeat;
  background-size: 430px 596px;
  background-position: 479px 39px;
}
.slide-albania {
  background: url(../img/albania-slide.jpg) no-repeat;
  background-size: 430px 596px;
  background-position: 480px 40px;
}
.slide-macedonia {
  background: url(../img/macedonia-slide.jpg) no-repeat;
  background-size: 430px 596px;
  background-position: 481px 41px;
}
.left-slide__heading {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 55px;
  line-height: 90px;
  font-weight: 500;
  color: #2c2e3f;
  margin-bottom: 50px;
}
.left-slide__heading::after {
  content: '';
  position: absolute;
  width: 175px;
  height: 3px;
  background-color: #fe7865;
  top: 105px;
  left: 0px;
}
.left-slide__text {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: #2c2e3f;
  opacity: 0.7;
  width: 370px;
  margin-bottom: 50px;
}
.left-slide__link {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  width: 250px;
  height: 75px;
  background-color: #fe7865;
}
.slide-feedback {
  background-color: #ffffff;
  width: 382px;
  height: 367px;
  margin-left: 418px;
  margin-top: 150px;
}
.slide-feedback__heading {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
  line-height: 36px;
  font-weight: 500;
  color: #2c2e3f;
  margin-top: 30px;
  margin-bottom: 32px;
  margin-left: 30px;
}
.slide-feedback__heading::after {
  content: '';
  position: absolute;
  width: 68px;
  height: 1px;
  background-color: #fe7865;
  top: 52px;
  left: 0px;
}
.slide-feedback__text {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 28px;
  font-weight: 400;
  color: #2c2e3f;
  opacity: 0.7;
  width: 322px;
  margin-bottom: 44px;
  margin-left: 30px;
}
.slide-feedback__person {
  font-family: 'Montserrat', sans-serif;
  /*  ========= Тут надо подключить другой шрифт!!!!  ========= */
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #000000;
  padding-left: 84px;
}
